<template>
    <div class="chart-world"> 
      <h1>chart-world</h1>    
             <section>
         <div> 
<button class="tablink" @click="changePeriod('5d');">5d</button>
<button class="tablink" onclick="openPage('News', this, 'green')" id="defaultOpen">20d</button>
<button class="tablink" onclick="openPage('Contact', this, 'blue')">3M</button>
<button class="tablink" onclick="openPage('About', this, 'orange')">1Y</button>
             </div> 
             </section>

         <div>  
        <label for="ltype">read file</label> 
        <input type="checkbox" id="get-read-file" name="ltype"  checked><br>

        <label for="lname">start date:</label> 
        <input type="text" id="start-date" name="lname">

        <button id="next" @click="getNext">next</button>
        <button id="get-curr" @click="getData">get</button>

        <label for="items">item:</label> 
            <select v-model="selectedItem" @change="changeItem">
                <option v-for="item in items" :value="item.value" :key="item.value">
                        {{ item.text }}
                </option>
            </select> 
        
            <section> 
                <div class="content-cities">  
                    <a v-for="city in cities" :key="city.id"
                                :class="['button button1', city.checked ? 'button-checked' : '']"
                                @click="city.checked = !city.checked; changeItem();"> 
                                {{city.name}} 
                    </a>  
                </div>
            </section> 
        </div>  

        <section> 
            <div class="box" id="line-chart"></div>   
        </section>   
        <section>
            <div id="data-table"></div>
        </section>  
            
        <section>
            <div class="box" id="bar-chart"></div>  
        </section>   
        <section>
            <div class="box" id="pack-chart"></div>  
        </section> 
        <section>  
            <div class="box" id="pie-chart"></div>  
        </section>  
    </div>
</template>

<script>  
import * as d3 from "d3" 
import XmlToJson from "../Util/XmlToJson.js" 
import LineChart from '../modules/LineChart.js'
import { namesId, coColumns, color, MngDataDate,  MngDataGubun} from '../Util/utilCo.js'

const ERROR_READ = "error"

export default {
    name: 'ChartWorld',
    props: { 
    },
    data() {
        return {
            data: null,
            selectedItem : 'deathCnt', 
            items : [{text:"확진자", value:"defCnt"}, 
                        {text:"사망자",  value:"deathCnt"}],  
            cities : namesId,
            selectedPeriod: '5d',
            chart : {
                id : "line-chart",
                width : 600,
                height : 600,    
                duration: 3000, //transition  
                delay: 3000,
                ease: d3.easeLinear, 
                margin:{top:50, bottom:50, left:50, right:50},
                nVisible: 17,
                textColor: 'black',
                gradient: {
                    color1 : "red",
                    color2 : "blue",
                    threshold: 1500000 //기준값이되고, 없으면 median 으로 함
                },
                line: true,
                label: true,
                axis: true,
                //dot: true
            },
            lineChart: null,
            mngDataDate: null,
            mngDataGubun: null
        }
    },
    mounted() {     //6886816

    }, 
    methods: { 
        changePeriod() {
            //const period = this.selectedPeriod;
        },
        getNext() {
             this.lineChart.updateTest(this.selectedItem)
        },
        changeItem() {  
            const item = this.selectedItem;//items.options[items.selectedIndex].value 
            
            if(this.data) {
                this.mngDataDate.changeDataset(item)  
                this.mngDataGubun.changeDataset(item)  

                //날짜와 상관엇이
                //처음 읽은 다음에 그린다.
                this.updateLine(this.mngDataGubun.getDataset(), 
                                this.mngDataGubun.getDates(),
                                this.selectedItem)   
                    
                //updateCovid19(); 
            }
        }, 
        updateLine(dataset, dates, item, callback) {  
            console.log(dataset)
             const namesId = this.cities;
           

             let _dataset = [];// = _dataset; 

            console.log(namesId)
            //this.dataset.map(d => d.id = UID())
            dataset = dataset.filter(d => d.name !== "합계");
            dataset.forEach(d => {
                const  a = namesId.filter(n => n.name === d.name && n.checked)
                //console.log(a)
                if(a.length > 0) _dataset.push(d)
            }); 

            if(this.lineChart === null) {
                this.lineChart = new LineChart(this.chart); 
            } 

            let lineChart = this.lineChart;
             
            async function f() { 
                let promise = new Promise((resolve, reject) => {
                    //setTimeout(() => resolve("완료!"), 1000)
                    lineChart.update({ 
                        coColumns: coColumns,
                        dataset: _dataset, 
                        _dates: dates,
                        _color: color,
                        item: item,
                        namesId: namesId, 
                        //xTickArguments: [d3.timeMonth.every(6)],
                        //xTickFormat: d3.timeFormat("%Y-%m-%d"), 
                        //xTickArguments: [d3.timeMonth.every(3)],
                        //xTickFormat: d3.timeFormat("%B"), 
                        xTickArguments: [d3.timeDay.every(10)],
                        xTickFormat: d3.timeFormat("%Y-%m-%d"), 
                        //.ticks(d3.timeMonth.every(3)) 
                        // .tickFormat(d3.timeFormat("%B"))
                        // .ticks(d3.timeDay.every(1)) 
                        //  .tickFormat(d3.timeFormat("%d"))
                        //.tickSizeOuter(0),
                        callback: resolve,
                        cc: reject 
                        //_rooted:false, //선택
                        //_sorted:false //선택
                    })  
                });

                await promise; // 프라미스가 이행될 때까지 기다림 (*)
        
                if(callback) callback("lineChart"); 
            }
            f() 
        },  
        readDone(data) { 
            if(data) {
                this.data = data;
                if(ERROR_READ === data.error) { 
                    alert(data.msg)
                    return;
                }

                this.mngDataDate = new MngDataDate(); 
                this.mngDataGubun = new MngDataGubun();
                
                this.mngDataDate.setData(data)  
                this.mngDataGubun.setData(data) 

                this.changeItem();
            }  
        }, 
        getData() { 
            this.getCovid19SidoInfStateJson( "20210501",  "20210511", this.readDone)
           // this.readCovid19SidoInfStateJson("co.json", this.readDone)
        },
        getCovid19SidoInfStateJson(startCreateDt, endCreateDt, callback) {
            var xml2json = new XmlToJson();  
            console.log("*Get Covid19SidoInfStateJson*"); 
            
            const URL = "http://openapi.data.go.kr/openapi/service/rest/Covid19/getCovid19SidoInfStateJson";
            const SERVICE_KEY = "d5B8D5%2BsGHBOdOZtEeUxA8kBuqzPQgjtQBwZCpEf4R7Vw1YwhBbg2Lzvy1YH8mV3uCXkkI0AaDwAACBbH0IRhw%3D%3D";

            var numOfRows = "1";
            var pageNo = "1";
            //var startCreateDt = "20210625" //"20200101";
            //var endCreateDt = "20210625";//getToday();
 
            var url = `${URL}?serviceKey=${SERVICE_KEY}&pageNo=${pageNo}&numOfRows=${numOfRows}&startCreateDt=${startCreateDt}&endCreateDt=${endCreateDt}`
                          
            function getCovid(url) {
                return fetch(url)  
                .then(response => response.text())    
                .then(data => xml2json.fromStr(data).response.body.items.item )
                .catch(err => {
                    console.error("Error", err);
                }); 
            } 

            async function getNextCovid(url) {
                const data = await getCovid(url)
                data.map(d => d.date = d.createDt.slice(0,10))
                callback(data)  
            }  

            getNextCovid(url)  
        },
        readCovid19SidoInfStateJson(fileJson, callback){
            async function read(fileJson){
                let promise = new Promise((resolve, reject) => {  
                    d3.json(fileJson) 
                    .then(function(data) {   
                        resolve(data)
                    })
                    .catch(function(e) {  
                        reject({error:ERROR_READ, msg:e})
                    })
                }) 

                promise.then(data => callback(data))
                .catch(e => callback(e)) 
            } 
            read(fileJson) 
        } 
  } 
}
</script>

