    display:flex;
    justify-content: center;
    align-items: center;
    min-height: 20vh;
}     
.box{
    position: relative;
    min-width: 600px;  
    padding: 20px;
    box-shadow: 0 35px 50px rgb(0,0,0,0.1);
}   
  
