    background-color: yellow;
}
.tdBar { 
    background-color: rgb(4, 170, 109, 0.3);
    border-radius: 4%; 
    position: relative; 
}
.content-cities{
    max-width: 600px;
} 
.button {
  background-color: #008CBA; /* Green */
  border: none;
  color: white;
  padding:  6px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
  border-radius: 16px;
}

.button1 {
  background-color: white; 
  color: black; 
  border: 2px solid #008CBA;
}

.button1:hover {
  background-color: #008CBA;
  color: white;
}
 .button-checked {background-color: #008CBA;color: white;} 
