export function getDate(date, inc = 0) {
  //today
  if(!date)
    return formatDate(new Date());
 
  if(typeof(date) === "string") date = new Date(date);

  date.setDate(date.getDate() + inc)
  //dateStr = date.toISOString().substring(0, 10); 
   
  return formatDate(date);
}

