//기간설정 안의 random 날짜
//randomDate(new Date(2021, 3, 1), new Date(2021, 3, 30));
export function getRandomDate(startDate, endDate){
    function randomDate(start, end) {
        return new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()));
    } 
    const d = randomDate(startDate, endDate);
    //console.log();
    return d.toISOString().substring(0, 10);
}

export //작은원에서 큰원으로 이동시 같은 각도의 위치 찻기
function getPosTranslate(data, move=2) {
  const moveAngle = data.startAngle + ((data.endAngle - data.startAngle) / 2);
  return `translate(${- move * Math.cos(moveAngle + Math.PI / 2)}, ${- move * Math.sin(moveAngle + Math.PI / 2)})`;
}

