//한줄 텍스트를 분리해서 여러줄로 만들기
//value 는 true 설정
//text = aaa bbb. value = 2345
// 예) pcak 에서 사용됨
export function splitLines(text, value){ 
    //let text =  d.data.name;//"aaa bbb";
   
    const lines = text.split(/\s+/g).map(d=> { 
            return{ 
                text:d 
    }})
    //맨마지막에 value 넣기
    if (value !== undefined){
      lines.push({text:value, value:true});
    }
    return lines;
  }
   
  export function appendLinearGradient(svg, arr){ 
    if(arr.length<=0) return
  
    let keys =  arr.map(d=>d.id)
    let colors =  arr.map(d=>d.color)   
    
    svg.select(".defsLinearGradient").remove() 
    let g = svg.append("g") 
    .attr("class", "defsLinearGradient")
     
     
    for(let i = 0; i < keys.length; i++){
      let grad_id = `${keys[i]}` 
      //console.log("grad_id", i, grad_id)
      if (svg.select(grad_id)._groups[0][0] === undefined) {  
    
        let linearGradient =  g.append("defs")
        .append("linearGradient")
        .attr("id", grad_id) 
        //let linearGradient = svg.select(`#${grad_id}`) 
        .attr("x1", "-50%") .attr("y1", "-50%") .attr("x2", "100%")  .attr("y2", "100%")
        //.attr("x1", "0%").attr("y1", "0%").attr("x2", "100%").attr("y2", "0%")
        linearGradient.append("stop")
        .attr("offset", "0%")
        .attr("style", "stop-color:rgb(255,255,255);stop-opacity:1")
        linearGradient.append("stop")
        .attr("offset", "100%")//tableau10
        //   .attr("style", `stop-color:red;stop-opacity:1`) 
        .attr("style", `stop-color:${colors[i]};stop-opacity:1`)
        //  .attr("style", `stop-color:${data[i].color};stop-opacity:1`)
      }
    } 
  }
