 
import * as d3 from "d3" 
import { gradientUID } from '../Util/util.js'  
 
const CNAME_LINE = "line";
const CNAME_DOTS = "dots";
const CNAME_LABEL = "label";
const CNAME_DOTS_LABEL = "dots-label";

//-- 
export function appendLabel(svg) {   
    const g = svg.append("g").attr("class", CNAME_LABEL)  

    
function getDate(a, data) {
    data.forEach(function(e){
         //const a = new Date("2022-01-01")
        //const b = new Date("2022-01-02")
        const b = e.date;
        console.log(a.getTime(), b.getTime())
        if(a.getTime() === b.getTime())
        console.log("====")
        else if ((a.getTime() > b.getTime())) {

            //console.log("a")
            return true;
        }
        else if ((a.getTime() < b.getTime())) {
            //console.log("b")
            return false;
        }
    
    })
   
}

    return (transition, dataset, x, y, item, dates) => { 
        console.log("UPDATE LABEL", dataset[0].data)
        const startTransition = text =>
            text.transition(transition)
            .attrTween("transform", function(d){  
                const path = svg.select(`.${CNAME_LINE}${d.id}`).node() //".line" +  d.id).node() 
                let length = path.getTotalLength(); 
                return function(t) {
                    let p = path.getPointAtLength(t * length);
                    //console.log(p.x)

                    const xm = x.invert(p.x);  
                    const index = d3.bisectCenter(dates, xm); 
                    let date;
                    if(index>0) {
                        date = dates[index];

                        if(date < 0) console.log(date)
                        getDate(date, dataset[0].data)
                    //dataset에서 날짜 찻기
                    //찻은날짜로 text 만들기
                   // dataset.data
                    }

                    return "translate(" + p.x + "," + p.y + ")";
                }; 
            })  

        const updateTransition = text =>  
            text.transition(transition)
            .attr("transform", function(d) {
                const p1 = x(d.data[d.data.length-1].date)
                const p2 = y(d.data[d.data.length-1][item])  
                return `translate(${p1}, ${p2})` 
            })  

        const removeTransition = text =>
            text.transition(transition)
            .attrTween("transform", function(d){  
                const path = svg.select(".line" +  d.id).node() 
                let length = path.getTotalLength(); 
                return function(t) { 
                    let p = path.getPointAtLength((1-t) * length);
                    return "translate(" + p.x + "," + p.y + ")";
                }; 
            })
            .remove()

        g.selectAll("text") 
        .data(dataset, d => d.name) 
        .join(enter => enter.append("text")
                .attr("class", d => "text" + d.id) 
                .attr("text-anchor", "start")
                .attr("fill", "none")   
                .text(d => d.name) 
                .attr("stroke",  "black") 
                .call(startTransition),  
            update => update.call(updateTransition),
            exit => exit.call(removeTransition)             
        )
        //.text(d => d.name) 
        //.attr("stroke",  "black")  
    } 
}
 
function mouseEaseElastic(self, attr, size) {     
    d3.select(self)
    .transition()
    .duration(500)
    .ease(d3.easeElastic)
   // .attr('r', r)  
    .attr(attr, size) //'font-size', r)  
} 
