export class MngData {   
  constructor() { 
    this.dataset = null; 
    this.dates = null;  
  }
  setData(data) {  
    data.map(d => d.date = new Date(d.createDt.slice(0,10))) 
       
    //숫자 컬럼인거는 문자에서 숫자로 변환해주기
    let numberColumns = coColumns.filter(d => d.type === "number")
    numberColumns.map(function(num) { 
      data.map(d => d[num.name] = parseInt(d[num.name]))
    })  
    this.data = data;  
    
    this.dates = Array.from(new Set(Array.from(this.data.map(d => formatDate(d.date)))))  
                .sort((a, b) => d3.ascending(a, b))
                this.dates = this.dates.map(d => d = new Date(d)) 
  }
  
  //dates  객체
  getDates = () => this.dates; 
  //전체데이터
  getData = () => this.data;

  getDataset = () => this.dataset;
  
  isDate = (dateStr) => this.dates.filter(d => formatDate(d.date) === dateStr) //.indexOf(dateStr) 
  //[Array(2), Array(2) ...] : Array(2) -> ["서울", InternMap(19) {"2022-01-01"(Data 객체) => 1, '"2022-01-02"(Data 객체) => 43,...]
    
  getDatasetType = (item, field1, field2) => 
    Array.from(d3.rollup(this.data,([d]) => d[item], d => d[field1], d => d[field2]))    
  
}
export class MngDataDate extends MngData{   
  constructor() {  
      super() 
    }  
    changeDataset = (item) =>   //1. TYPE_DATE
                              //[Array(2), Array(2) ...] : Array(2) -> ["2022-01-01"(Data 객체), InternMap(19) {'합계' => 1, '서울' => 0,...]
      this.dataset = this.getDatasetType(item, 'date', 'gubun') 
                    .sort(([a],[b]) => d3.ascending(a, b))   //날짜별 정렬
                   // .sort((a, b) => d3.descending(a[0], b[0]))  //날짜별 정렬 
                    //d3.ascending(a, b))  

    getDataFromDataset(findstr) { 
        return Array.from( this.dataset.filter(d => formatDate(d[0]) === findstr)[0][1]) 
    }  
    
  //한줄 전체
  getDataRowFromData = (str) => this.data.filter(d => typeof(d.date) === "string" ? d.date === str : formatDate(d.date) === str)
   
  }

  
